import { motion } from "framer-motion";
import React from "react";
import "../styles/main.css";

const Header = ({ isLoading = false, showHeroText = false }) => (
  <motion.header
    className="header"
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    transition={{ duration: 1 }}
  >
    {/*    */}
    <motion.div 
      className="hero-image"
      initial={{ opacity: isLoading ? 0 : 1 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.8, delay: isLoading ? 0.5 : 0 }}
    >
      <img src={`${process.env.PUBLIC_URL}/images/combined-webp/hero_image.webp`} alt="" />
      
      {/*     - showHeroText true   */}
      {showHeroText && (
        <div className="hero-overlay">
          <motion.div 
            className="hero-text"
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, delay: 0.3 }}
          >
            <h1 className="hero-title">our<br/>wedding<br/>day</h1>
            
            <div className="hero-date">
              <p> 2025 / 09 /06 </p>
              <p>Two hearts, one journey,</p>
              <p>United in love, together forever.</p>
            </div>
          </motion.div>
        </div>
      )}
    </motion.div>

    {/*    -     */}
    {!isLoading && (
      <motion.div 
        className="greeting-section"
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8, delay: 0.5 }}
      >
        <h2 className="greeting-title"> </h2>
        
        <div className="greeting-content">
          <p>  </p>
          <p>   ,</p>
          <p>  </p>
          <p> .</p>
          <br />
          <p>     ,</p>
          <p>  .</p>
        </div>

      </motion.div>
    )}
  </motion.header>
);

export default Header; 