import { useInView } from "react-intersection-observer";
import { motion } from "framer-motion";
import React from "react";
import "../styles/main.css";

const WeddingInfo = () => {
  const { ref, inView } = useInView({ triggerOnce: true, threshold: 0.2 });

  // 2025 9  
  const calendarDays = [];
  const firstDay = new Date(2025, 8, 1).getDay(); // 9 1  (0=)
  const daysInMonth = new Date(2025, 8 + 1, 0).getDate(); // 9 

  //    (  )
  for (let i = 0; i < firstDay; i++) {
    calendarDays.push(null);
  }

  //  
  for (let day = 1; day <= daysInMonth; day++) {
    calendarDays.push(day);
  }

  return (
    <motion.section
      className="wedding-info"
      ref={ref}
      initial={{ opacity: 0, y: 40 }}
      animate={inView ? { opacity: 1, y: 0 } : {}}
      transition={{ duration: 0.8 }}
    >
      <h2> </h2>
      
      <div className="wedding-details">
        <p className="wedding-date">2025 9 6   1</p>
        <p className="wedding-venue">   </p>
      </div>

      <div className="wedding-photo">
        <img src={`${process.env.PUBLIC_URL}/images/combined-webp/top_image.webp`} alt="" />
      </div>

      <div className="calendar-section">
        <h3>9</h3>
        <div className="calendar">
          <div className="calendar-header">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
          <div className="calendar-body">
            {calendarDays.map((day, index) => (
              <div
                key={index}
                className={`calendar-day ${day === 6 ? 'wedding-day' : ''} ${!day ? 'empty' : ''}`}
              >
                {day}
              </div>
            ))}
          </div>
        </div>
      </div>
    </motion.section>
  );
};

export default WeddingInfo; 