import React, { useState, useEffect } from "react";
import { useInView } from "react-intersection-observer";
import LoadingScreen from "./components/LoadingScreen";
import Header from "./components/Header";
import CoupleInfo from "./components/CoupleInfo";
import Countdown from "./components/Countdown";
import Gallery from "./components/Gallery";
import ErrorBoundary from "./components/ErrorBoundary";
// import TabSection from "./components/TabSection";
import WeddingInfo from "./components/WeddingInfo";
// import GuestSnap from "./components/GuestSnap";
import Directions from "./components/Directions";
import AccountInfo from "./components/AccountInfo";
import Footer from "./components/Footer";
import ScrollToTop from "./components/ScrollToTop";
import PetalFall from "./components/PetalFall";
import "./styles/main.css";

function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [showHeroText, setShowHeroText] = useState(false);

  //    ref
  const { ref: weddingInfoRef, inView: weddingInfoInView } = useInView({
    threshold: 0.1, // 10%  
    rootMargin: '0px 0px 0px 0px' //  
  });

  //    ref
  const { ref: directionsRef, inView: directionsInView } = useInView({
    threshold: 0.1, // 10%  
    rootMargin: '0px 0px 0px 0px'
  });

  const handleLoadingComplete = () => {
    setIsLoading(false);
    //    hero  
    setTimeout(() => {
      setShowHeroText(true);
    }, 200);
  };

  //       
  useEffect(() => {
    const lastLoadingTime = sessionStorage.getItem('lastLoadingTime');
    const now = Date.now();
    
    if (lastLoadingTime && (now - parseInt(lastLoadingTime)) < 5000) {
      //    
      setIsLoading(false);
      setShowHeroText(true);
    }
  }, []);

  //    (      )
  useEffect(() => {
    const handleGlobalError = (event) => {
      //     
      if (event.filename && (
        event.filename.includes('maps.google') ||
        event.filename.includes('maps.googleapis.com') ||
        event.filename.includes('embed')
      )) {
        console.error('   :', event.error);
        event.preventDefault(); //   
        return false;
      }
      
      //  JavaScript 
      if (event.error && event.error.message) {
        console.error('  :', event.error.message);
      }
    };

    const handleUnhandledRejection = (event) => {
      console.error('  Promise rejection:', event.reason);
      event.preventDefault(); //   
    };

    window.addEventListener('error', handleGlobalError);
    window.addEventListener('unhandledrejection', handleUnhandledRejection);

    return () => {
      window.removeEventListener('error', handleGlobalError);
      window.removeEventListener('unhandledrejection', handleUnhandledRejection);
    };
  }, []);

  return (
    <div className="App">
      {/* Hero   ,    */}
      <Header isLoading={isLoading} showHeroText={showHeroText} />
      
      {/*    */}
      {isLoading && <LoadingScreen onComplete={handleLoadingComplete} />}
      
      {/*    -      */}
      {!isLoading && <PetalFall isActive={weddingInfoInView && !directionsInView} petalCount={15} />}
      
      {/*       */}
      {!isLoading && (
        <>
          <CoupleInfo />
        
          
          {/* <TabSection /> */}
          <div ref={weddingInfoRef}>
            <WeddingInfo />
            <Countdown />
            <ErrorBoundary>
              <Gallery />
            </ErrorBoundary>
            {/* <Timeline /> */}
          </div>
          
          <div ref={directionsRef}>
            {/* <GuestSnap /> */}
            <ErrorBoundary>
              <Directions />
            </ErrorBoundary>
            <AccountInfo />
            <Footer />
          </div>
        </>
      )}
      
      {/*   -   */}
      <ScrollToTop />
    </div>
  );
}

export default App;
