import { useInView } from "react-intersection-observer";
import { motion, AnimatePresence } from "framer-motion";
import React, { useState } from "react";
import "../styles/main.css";

const AccountInfo = () => {
  const { ref, inView } = useInView({ triggerOnce: true, threshold: 0.2 });
  const [groomExpanded, setGroomExpanded] = useState(false);
  const [brideExpanded, setBrideExpanded] = useState(false);

  const groomAccounts = [
    { name: "", account: "177-910488-38707", bank: "", holder: "" },
    { name: " ", account: "173076-56-053481", bank: "", holder: "" },
    { name: " ", account: "110-258-800200", bank: "", holder: "" }
  ];

  const brideAccounts = [
    { name: "", account: "3333-09-3783127", bank: "", holder: "" },
    { name: " ", account: "016-02-154710", bank: "", holder: "" },
    { name: " ", account: "567-810570-43507", bank: "", holder: "" }
  ];

  const copyToClipboard = async (text, name) => {
    try {
      await navigator.clipboard.writeText(text);
      alert(`${name}  .`);
    } catch (err) {
      console.error(' :', err);
      alert(' .');
    }
  };

  const AccountSection = ({ title, accounts, expanded, setExpanded }) => (
    <div className="account-section">
      <button 
        className="account-header"
        onClick={() => setExpanded(!expanded)}
      >
        <span className="account-title">{title}</span>
        <span className={`account-arrow ${expanded ? 'expanded' : ''}`}>
          
        </span>
      </button>
      
      <motion.div
        className="account-content"
        initial={false}
        animate={{ 
          height: expanded ? "auto" : 0,
          opacity: expanded ? 1 : 0
        }}
        transition={{ 
          duration: 0.3,
          ease: "easeInOut"
        }}
        style={{ overflow: "hidden" }}
      >
        <div style={{ padding: expanded ? undefined : 0 }}>
          {accounts.map((account, index) => (
            <div key={index} className="account-item">
              <div className="account-info-left">
                <div className="account-name">{account.name}</div>
                <div className="account-number">{account.account}</div>
                <div className="account-bank">{account.bank} {account.holder}</div>
              </div>
              <div className="account-actions">
                <button 
                  className="copy-btn"
                  onClick={() => copyToClipboard(account.account, account.name)}
                >
                  
                </button>
              </div>
            </div>
          ))}
        </div>
      </motion.div>
    </div>
  );

  return (
    <motion.section
      className="account-info"
      ref={ref}
      initial={{ opacity: 0, y: 40 }}
      animate={inView ? { opacity: 1, y: 0 } : {}}
      transition={{ duration: 0.8 }}
    >
      <h2>  </h2>
      
      <div className="account-container">
        <AccountSection 
          title=""
          accounts={groomAccounts}
          expanded={groomExpanded}
          setExpanded={setGroomExpanded}
        />
        
        <AccountSection 
          title=""
          accounts={brideAccounts}
          expanded={brideExpanded}
          setExpanded={setBrideExpanded}
        />
      </div>
      
      <p className="thanks">   ,</p>
      <p className="thanks">   .</p>
    </motion.section>
  );
};

export default AccountInfo; 