import { useInView } from "react-intersection-observer";
import { motion, AnimatePresence } from "framer-motion";
import React, { useState, useEffect, useRef, useCallback } from "react";
import "../styles/main.css";

//    (   )
const imageFileNames = [
  '1Y4A2333.webp',
  '1Y4A2641.webp',
  '1Y4A2679.webp',
  '1Y4A2748.webp',
  '1Y4A2769.webp',
  '1Y4A2799.webp',
  '1Y4A2968.webp',
  '1Y4A3199.webp',
  '1Y4A3216.webp',
  '1Y4A3500.webp',
  '1Y4A3553.webp',
  '1Y4A3571.webp',
  '1Y4A3716.webp',
  '1Y4A3749.webp',
  '1Y4A3832.webp',
  '1Y4A3861.webp',
  '1Y4A3878.webp',
  '1Y4A3889.webp',
  '1Y4A3911.webp'
];

//    (public  webp  )
const allImages = imageFileNames.map((fileName, index) => {
  return {
    id: index + 1,
    src: `${process.env.PUBLIC_URL}/images/gallery-webp/${fileName}`,
    alt: `  ${index + 1}`,
    title: `  ${index + 1}`
  };
});

// Shuffle the allImages array to display images in random order
const shuffleArray = (array) => {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
};

shuffleArray(allImages);

const Gallery = () => {
  const { ref, inView } = useInView({ triggerOnce: true, threshold: 0.2 });
  const [selectedImage, setSelectedImage] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isExpanded, setIsExpanded] = useState(false);
  const [additionalImagesLoaded, setAdditionalImagesLoaded] = useState(false);
  const [preloadedImages, setPreloadedImages] = useState(new Set());
  const [isTransitioning, setIsTransitioning] = useState(false);
  const galleryGridRef = useRef(null);
  const preloadingRef = useRef(false);
  const preloadedImagesRef = useRef(new Set());
  const initialPreloadDone = useRef(false);
  const additionalPreloadDone = useRef(false);
  const transitionTimeoutRef = useRef(null);

  //      Intersection Observer
  const { ref: loadTriggerObserverRef, inView: nearEnd } = useInView({
    threshold: 0.1,
    triggerOnce: false
  });

  //   (  )
  const displayImages = isExpanded ? allImages : allImages.slice(0, 12);

  //     ( )
  const preloadImageSafely = useCallback((imageSrc, timeout = 10000) => {
    return new Promise((resolve, reject) => {
      if (preloadedImagesRef.current.has(imageSrc)) {
        resolve(imageSrc);
        return;
      }

      const img = new Image();
      const timeoutId = setTimeout(() => {
        img.onload = null;
        img.onerror = null;
        reject(new Error(`Image load timeout: ${imageSrc}`));
      }, timeout);

      img.onload = () => {
        clearTimeout(timeoutId);
        preloadedImagesRef.current.add(imageSrc);
        setPreloadedImages(prev => new Set([...prev, imageSrc]));
        resolve(imageSrc);
      };

      img.onerror = (error) => {
        clearTimeout(timeoutId);
        console.warn(`  : ${imageSrc}`, error);
        reject(error);
      };

      img.src = imageSrc;
    });
  }, []); //   

  //     ( )
  const preloadImagesBatch = useCallback(async (images, batchSize = 3) => {
    if (preloadingRef.current) return;
    preloadingRef.current = true;

    try {
      for (let i = 0; i < images.length; i += batchSize) {
        const batch = images.slice(i, i + batchSize);
        const batchPromises = batch.map(image => 
          preloadImageSafely(image.src).catch(err => {
            console.warn(`   :`, err);
            return null;
          })
        );
        
        await Promise.allSettled(batchPromises);
        
        //        
        if (i + batchSize < images.length) {
          await new Promise(resolve => setTimeout(resolve, 100));
        }
      }
      console.log('   ');
    } catch (error) {
      console.error('    :', error);
    } finally {
      preloadingRef.current = false;
    }
  }, []); //   

  const openModal = (index) => {
    setCurrentIndex(index);
    setSelectedImage(allImages[index]);
  };

  const closeModal = () => {
    setSelectedImage(null);
  };

  const nextSlide = () => {
    if (isTransitioning) return; //    
    
    //    
    if (transitionTimeoutRef.current) {
      clearTimeout(transitionTimeoutRef.current);
    }
    
    setIsTransitioning(true);
    const newIndex = (currentIndex + 1) % allImages.length;
    setCurrentIndex(newIndex);
    setSelectedImage(allImages[newIndex]);
    
    //    
    transitionTimeoutRef.current = setTimeout(() => {
      setIsTransitioning(false);
      transitionTimeoutRef.current = null;
    }, 350); //    
  };

  const prevSlide = () => {
    if (isTransitioning) return; //    
    
    //    
    if (transitionTimeoutRef.current) {
      clearTimeout(transitionTimeoutRef.current);
    }
    
    setIsTransitioning(true);
    const newIndex = (currentIndex - 1 + allImages.length) % allImages.length;
    setCurrentIndex(newIndex);
    setSelectedImage(allImages[newIndex]);
    
    //    
    transitionTimeoutRef.current = setTimeout(() => {
      setIsTransitioning(false);
      transitionTimeoutRef.current = null;
    }, 350); //    
  };

  //      ()
  const handleAnimationComplete = useCallback(() => {
    if (transitionTimeoutRef.current) {
      clearTimeout(transitionTimeoutRef.current);
      transitionTimeoutRef.current = null;
    }
    setIsTransitioning(false);
  }, []);

  const toggleExpanded = useCallback(() => {
    const wasExpanded = isExpanded;
    setIsExpanded(!isExpanded);
    
    //    ( )    
    if (!wasExpanded && !additionalPreloadDone.current && !preloadingRef.current) {
      console.log('  -     ');
      additionalPreloadDone.current = true;
      const additionalImages = allImages.slice(12);
      
      //    
      preloadImagesBatch(additionalImages).then(() => {
        setAdditionalImagesLoaded(true);
      }).catch(err => {
        console.error('   :', err);
      });
    }
  }, [isExpanded]); //  

  //         (  )
  useEffect(() => {
    if (nearEnd && !isExpanded && !additionalPreloadDone.current && !preloadingRef.current) {
      console.log('    -    ');
      additionalPreloadDone.current = true;
      const additionalImages = allImages.slice(12);
      
      preloadImagesBatch(additionalImages).then(() => {
        setAdditionalImagesLoaded(true);
      }).catch(err => {
        console.error('   :', err);
      });
    }
  }, [nearEnd, isExpanded]); //  

  //  /    
  useEffect(() => {
    if (selectedImage) {
      //     
      document.body.style.overflow = 'hidden';
    } else {
      //     
      document.body.style.overflow = 'unset';
    }
    
    //     
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [selectedImage]);

  //    ( 12 ,   )
  useEffect(() => {
    if (inView && !initialPreloadDone.current && !preloadingRef.current) {
      initialPreloadDone.current = true;
      const initialImages = allImages.slice(0, 12);
      console.log('    ');
      
      preloadImagesBatch(initialImages).catch(err => {
        console.error('   :', err);
      });
    }
  }, [inView]); //  

  //   
  useEffect(() => {
    const handleError = (event) => {
      if (event.target && event.target.tagName === 'IMG') {
        console.error('  :', event.target.src);
      }
    };

    document.addEventListener('error', handleError, true);
    
    return () => {
      document.removeEventListener('error', handleError, true);
    };
  }, []);

  //    cleanup
  useEffect(() => {
    return () => {
      //    
      preloadingRef.current = false;
      
      //   
      if (transitionTimeoutRef.current) {
        clearTimeout(transitionTimeoutRef.current);
      }
      
      //  
      document.body.style.overflow = 'unset';
      
      console.log('  cleanup ');
    };
  }, []);

  return (
    <motion.section
      className="gallery"
      ref={ref}
      initial={{ opacity: 0 }}
      animate={inView ? { opacity: 1 } : {}}
      transition={{ duration: 1.0, ease: "easeOut" }}
    >
      <h2></h2>
      
      <div className="gallery-grid" ref={galleryGridRef}>
        {displayImages.map((image, index) => (
          <motion.div
            key={image.id}
            className="gallery-item"
            onClick={() => openModal(index)}
            whileTap={{ scale: 0.95 }}
            initial={{ opacity: 0, y: 20 }}
            animate={inView ? { opacity: 1, y: 0 } : {}}
            transition={{ 
              duration: index < 12 ? 0.8 : 0.5, //   
              delay: index < 12 ? index * 0.15 : 0, //    (0.1  0.15)
              ease: "easeOut" //  easing 
            }}
          >
            <img 
              src={image.src} 
              alt={image.alt}
              loading={index < 6 ? "eager" : "lazy"}
              decoding="async"
              onError={(e) => {
                console.warn(`   : ${image.src}`);
                e.target.style.display = 'none'; //   
              }}
              onLoad={() => {
                preloadedImagesRef.current.add(image.src);
                setPreloadedImages(prev => new Set([...prev, image.src]));
              }}
            />
          </motion.div>
        ))}
      </div>

      {/*        (    ) */}
      {!isExpanded && (
        <div 
          ref={loadTriggerObserverRef}
          style={{ height: '1px', marginTop: '1rem' }}
        />
      )}

      {/* /  */}
      <motion.button
        className="toggle-btn"
        onClick={toggleExpanded}
        whileTap={{ scale: 0.95 }}
        whileHover={{ scale: 1.02 }}
        transition={{ duration: 0.2 }}
        style={{ marginTop: '2rem' }}
      >
        {isExpanded ? '' : ''} 
        <span className={`arrow ${isExpanded ? 'up' : 'down'}`}></span>
      </motion.button>

      {/*   */}
      <AnimatePresence>
        {selectedImage && (
          <motion.div
            className="gallery-modal"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={closeModal}
          >
            <motion.div
              className="modal-content"
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              onClick={(e) => e.stopPropagation()}
            >
              <button className="modal-close" onClick={closeModal}>
                
              </button>
              
              <button 
                className={`modal-btn prev ${isTransitioning ? 'disabled' : ''}`}
                onClick={prevSlide}
                disabled={isTransitioning}
              >
                &#8249;
              </button>
              
              <div className="modal-image-container">
                <AnimatePresence mode="wait" onExitComplete={handleAnimationComplete}>
                  <motion.img
                    key={currentIndex}
                    src={selectedImage.src}
                    alt={selectedImage.alt}
                    initial={{ opacity: 0, x: 100 }}
                    animate={{ opacity: 1, x: 0 }}
                    exit={{ opacity: 0, x: -100 }}
                    transition={{ duration: 0.3 }}
                    onError={(e) => {
                      console.warn(`   : ${selectedImage.src}`);
                    }}
                    onLoad={() => {
                      preloadedImagesRef.current.add(selectedImage.src);
                      setPreloadedImages(prev => new Set([...prev, selectedImage.src]));
                    }}
                  />
                </AnimatePresence>
              </div>
              
              <button 
                className={`modal-btn next ${isTransitioning ? 'disabled' : ''}`}
                onClick={nextSlide}
                disabled={isTransitioning}
              >
                &#8250;
              </button>
              
              <div className="modal-counter">
                <span style={{ opacity: isTransitioning ? 0.5 : 1, transition: 'opacity 0.3s' }}>
                  {currentIndex + 1} / {allImages.length}
                </span>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.section>
  );
};

export default Gallery; 